	module.exports = {"data":{"markdownRemark":{"html":"<p>저녁에는 한 학생이 내게 와서 \"그것에 대해 이야기하는 것\"이라고 말했다. 오늘날 학생들에게는 이상한 아이디어가 있습니다. 끊임없이 변화하는 사회와 끊임없이 느슨한 사회 구조에서 학생들은 스스로 선택해야합니다.</p>\n<p>발달의 길을 선택하기 위해 많은 학생들이 중등 학교를 떠나 자마자 일련의 중대한 삶 문제에 직면합니다. 그것은 부모님과 교사들에 의해 관리 되어야만했습니다. 사회 문화가 바뀌 었으니, 학생들 스스로 선택하는 것이 중요합니다. 혼란스럽고 선택과 모순으로 가득 찬 세계를 대면하는 법을 모르는 많은 학생들이 실제로 있습니다. 많은 사람들이 선택의 여지가 없어서 중요한 개발 기회를 잃었으며 일부는 조숙 한 선택 때문에 발전 기회를 잃어 버렸습니다. 그것은 좋은 선택이 아니며 좋은 선택이 아닙니다. 때로는 선택하고 때로는 선택하지 않는 경우도 있습니다. 젊은 사람이 결정하기 란 쉽지 않습니다.</p>","id":"/home/swain/workspace/political-life/src/pages/ko/0049.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ko/0049.md","frontmatter":{"title":"3월 4일 금요일"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ko/0049.md absPath of file >>> MarkdownRemark"}}

